import { useState } from "react";
import "./App.css";

function App() {
  const [input, setInput] = useState("");
  const [customMean, setCustomMean] = useState(null);
  const [customMeanInput, setCustomMeanInput] = useState(" ");
  const [result, setResult] = useState(null);
  const [average, setAverage] = useState(null);

  const parseInput = () => {
    let nums = [];
    let i = 0;
    let s = "";
    while (i <= input.length) {
      if (
        input.charAt(i) !== "," &&
        input.charAt(i) !== " " &&
        input.charAt(i) !== "\n"
      ) {
        s = s + input.charAt(i);
        i++;
        if (i === input.length) {
          nums.push(parseFloat(s));
        }
      } else {
        console.log(s);
        if (s.length > 0) {
          nums.push(parseFloat(s));
          s = "";
        }

        i++;
      }
    }

    if (customMeanInput !== " ") {
      let val = parseFloat(customMeanInput);
      if (val) {
        setCustomMean(val);
      } else {
        setCustomMeanInput("Invalid Input");
      }
    }

    return nums;
  };

  const calculate = () => {
    if (input === "") {
      return;
    }
    let set = parseInput();
    console.log(set);
    let n = set.length;
    let sum;
    let avg;
    if (customMean === null) {
      sum = 0;
      for (let i = 0; i < n; i++) {
        sum += set[i];
      }
      avg = sum / n;
    } else {
      avg = customMean;
    }

    let numerator = 0;
    for (let i = 0; i < n; i++) {
      let val = (set[i] - avg) * (set[i] - avg);
      numerator += val;
    }

    let newResult = numerator / (n - 1);
    setResult(newResult);
    setAverage(avg);

    //Variance = (E(Xi - x)^2) / n - 1
  };
  return (
    <div className="App">
      <span
        style={{
          fontSize: "5rem",
          position: "absolute",
          top: "2rem",
          left: "25vw",
        }}
      >
        STATISTICS CALCULATOR
      </span>
      <span className="inputAreaTitle">Enter your data set below...</span>
      <textarea
        rows={4}
        className="inputArea"
        placeholder="Seperate your elements with commas or spaces... Ex: 3, 4, 12, 29, 30.4, 20 "
        value={input}
        onChange={(e) => {
          setInput(e.target.value);
        }}
      />
      <div className="customMean">
        <label className="customMeanLabel">
          Enter Custom Mean Value (Optional):
        </label>
        <input
          className="customMeanInput"
          value={customMeanInput}
          onChange={(e) => {
            setCustomMeanInput(e.target.value);
          }}
        />
      </div>

      <button
        className="calculateButton"
        onClick={() => {
          calculate();
        }}
      >
        Calculate!
      </button>

      {result && average && (
        <div className="results">
          <span className="resultLabel">
            Variance (s^2) = <span className="resultNumber">{result}</span>
          </span>
          <span className="resultLabel">
            Standard Deviation (s) =
            <span className="resultNumber">{Math.sqrt(result)}</span>
          </span>
          <span className="resultLabel">
            Mean = <span className="resultNumber">{average}</span>
          </span>
        </div>
      )}
    </div>
  );
}

export default App;
